        position: sticky;
        top: 60px !important; // 
        z-index: 9;
        padding: 8px 20px;
        display: flex;
        align-items: center;
        gap: 10px;
        border-bottom: 1px solid #EBEEF5;
        background: rgba(255, 255, 255, 0.9);
        backdrop-filter: blur(10px);

        .filter-container {
            display: flex;
            gap: 8px;
            flex: 1;
            min-width: 0;

            .compact-select {
                width: auto;
                flex: 1;
            }

            .date-select {
                ::v-deep {
                    .el-input__inner {
                        font-size: 14px;
                    }
                }
            }

            .status-select {
                min-width: 200px; // 

                ::v-deep {
                    .el-input__inner {
                        font-size: 16px;
                    }

                    .el-select__tags {
                        display: flex !important;
                        flex-wrap: nowrap;
                        overflow: hidden;

                        .el-tag {
                            font-size: 16px;
                            background-color: transparent;
                            border: none;
                            padding: 0 4px;
                            height: auto;
                            line-height: normal;
                        }
                    }
                }
            }

            ::v-deep .filter-dropdown {
                min-width: 0;
                flex: 1;

                .filter-trigger {
                    .value {
                        font-size: 16px;
                    }
