/**
 * 
 * @param {Object} event 
 * @returns {string} 
 */
export function formatEventName(event) {
    if (!event) return '';

    let prefix = '';
    if (event.gender !== null) {
        prefix = event.gender ? '' : '';
    }

    const compType = {
        1: '()',
        2: '()',
        3: '()'
    }[event.compType] || '';

    return `${prefix}${event.name || event.eventName}${compType}`;
}

/**
 * 
 * @param {Object|string} scoreMap 
 * @returns {string} 
 */
export function formatScore(scoreMap) {
    if (!scoreMap) return ''

    //  scoreMap
    if (typeof scoreMap === 'string') {
        try {
            // -
            const pairs = scoreMap.replace(/[{}]/g, '').split(',')
            return pairs.map(pair => {
                const [unit, value] = pair.split('=')
                return `${value}${unit}`
            }).join('')
        } catch (e) {
            console.error(':', e)
            return ''
        }
    }

    //  scoreMap
    return Object.entries(scoreMap)
        .map(([unit, value]) => `${value}${unit}`)
        .join('')
}
