<template>
    <div class="registration-record">
        <div class="filter-section">
            <el-card>
                <div class="filter-header">
                    <h3></h3>
                    <el-button type="text" @click="resetFilters"></el-button>
                </div>
                <div class="filters">
                    <el-row :gutter="20">
                        <el-col :xs="24" :sm="12" :md="8" :lg="6">
                            <el-input v-model="filters.keyword" placeholder="/" clearable
                                prefix-icon="el-icon-search" @input="handleFilter" />
                        </el-col>
                        <el-col :xs="24" :sm="12" :md="8" :lg="6">
                            <el-select v-model="filters.event" placeholder="" clearable @change="handleFilter">
                                <el-option-group v-for="group in eventGroups" :key="group.label" :label="group.label">
                                    <el-option v-for="item in group.options" :key="item.sp_id" :label="item.name"
                                        :value="item.sp_id" />
                                </el-option-group>
                            </el-select>
                        </el-col>
                        <el-col :xs="24" :sm="12" :md="8" :lg="6">
                            <el-select v-model="filters.phase" placeholder="" clearable @change="handleFilter">
                                <el-option label="" value="preliminary" />
                                <el-option label="" value="semifinal" />
                                <el-option label="" value="final" />
                            </el-select>
                        </el-col>
                        <el-col :xs="24" :sm="12" :md="8" :lg="6">
                            <el-select v-model="filters.hasScore" placeholder="" clearable @change="handleFilter">
                                <el-option label="" value="true" />
                                <el-option label="" value="false" />
                            </el-select>
                        </el-col>
                    </el-row>
                </div>
            </el-card>
        </div>

        <div class="table-section">
            <el-card>
                <div class="table-header">
                    <div class="statistics">
                        <span> {{ filteredData.length }} </span>
                        <el-divider direction="vertical"></el-divider>
                        <span> {{ scoreCount }} </span>
                    </div>
                    <div class="actions">
                        <el-button type="primary" icon="el-icon-download" @click="exportToExcel">Excel</el-button>
                    </div>
                </div>

                <el-table :data="pagedData" stripe border style="width: 100%"
                    :default-sort="{ prop: 'timestamp', order: 'descending' }" v-loading="loading"
                    @sort-change="handleSortChange">
                    <el-table-column prop="timestamp" label="" width="180" sortable="custom"
                        :sort-orders="['ascending', 'descending']">
                        <template slot-scope="scope">
                            {{ formatTime(scope.row.timestamp) }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="studentId" label="" width="120" />
                    <el-table-column prop="name" label="" width="100" />
                    <el-table-column prop="school" label="" width="180" />
                    <el-table-column prop="eventName" label="" min-width="150">
                        <template slot-scope="scope">
                            <el-tag :type="getEventTagType(scope.row.eventPhase)">
                                {{ scope.row.eventName }}{{ getPhaseText(scope.row.eventPhase) }}
                            </el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="score" label="" width="120">
                        <template slot-scope="scope">
                            <template v-if="scope.row.score">
                                <span class="success-score">
                                    {{ formatScore(scope.row.score) }}
                                    <span class="point-text">({{ scope.row.point || '-' }})</span>
                                </span>
                            </template>
                            <el-tag v-else type="info" size="small"></el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="rank" label="" width="100">
                        <template slot-scope="scope">
                            <template v-if="scope.row.rank">
                                <el-tag :type="getRankTagType(scope.row.rank)" size="medium">
                                    {{ scope.row.rank }}
                                </el-tag>
                            </template>
                            <span v-else>-</span>
                        </template>
                    </el-table-column>
                </el-table>

                <div class="pagination">
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                        :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper" :total="filteredData.length">
                    </el-pagination>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
// 
function generateMockData() {
    const events = [
        { sp_id: 'SP001', name: '100', type: 'track' },
        { sp_id: 'SP002', name: '200', type: 'track' },
        { sp_id: 'SP003', name: '400', type: 'track' },
        { sp_id: 'SP004', name: '', type: 'field' },
        { sp_id: 'SP005', name: '', type: 'field' },
        { sp_id: 'SP006', name: '', type: 'field' }
    ];

    const schools = [
        '',
        '',
        '',
        '',
        '',
        ''
    ];
    const phases = ['preliminary', 'semifinal', 'final'];
    const records = [];

    // 200
    for (let i = 0; i < 200; i++) {
        const event = events[Math.floor(Math.random() * events.length)];
        const phase = phases[Math.floor(Math.random() * phases.length)];
        const hasScore = Math.random() > 0.3; // 70%

        records.push({
            id: i + 1,
            timestamp: Date.now() - Math.floor(Math.random() * 7 * 24 * 60 * 60 * 1000), // 7
            studentId: `2024${String(Math.floor(Math.random() * 10000)).padStart(4, '0')}`,
            name: `${i + 1}`,
            school: schools[Math.floor(Math.random() * schools.length)],
            eventName: event.name,
            eventType: event.type,
            sp_id: event.sp_id,  //  sp_id
            eventPhase: phase,  //  phase
            score: hasScore ? (event.type === 'track' ?
                (10 + Math.random() * 5).toFixed(2) : // 
                (4 + Math.random() * 4).toFixed(2)    // 
            ) : null,
            point: hasScore ? (Math.floor(Math.random() * 30) + 70) : null, //  70-100
            rank: hasScore ? Math.floor(Math.random() * 20) + 1 : null
        });
    }

    return records;
}

export default {
    name: 'RegistrationRecord',
    data() {
        return {
            // ...existing code...
            records: generateMockData(),
            filters: {
                keyword: '',
                event: '',
                phase: '',
                hasScore: ''
            },
            currentPage: 1,
            pageSize: 20,
            loading: false,
            sortBy: { prop: 'timestamp', order: 'descending' }
        }
    },

    computed: {
        eventGroups() {
            const groups = {
                track: { label: '', options: [] },
                field: { label: '', options: [] }
            };

            //  Set 
            const uniqueEvents = new Map(
                this.records.map(r => [r.sp_id, { sp_id: r.sp_id, name: r.eventName, type: r.eventType }])
            );

            uniqueEvents.forEach(event => {
                const option = {
                    sp_id: event.sp_id,
                    value: event.sp_id,
                    label: event.name,
                    name: event.name
                };

                if (event.type === 'track') {
                    groups.track.options.push(option);
                } else {
                    groups.field.options.push(option);
                }
            });

            return Object.values(groups);
        },

        filteredData() {
            return this.records.filter(record => {
                // 
                if (this.filters.keyword && !record.name.includes(this.filters.keyword) &&
                    !record.studentId.includes(this.filters.keyword)) {
                    return false;
                }

                // 
                if (this.filters.event && record.sp_id !== this.filters.event) {
                    return false;
                }

                // 
                if (this.filters.phase && record.eventPhase !== this.filters.phase) {
                    return false;
                }

                // 
                if (this.filters.hasScore !== '') {
                    const hasScore = record.score !== null;
                    return hasScore === (this.filters.hasScore === 'true');
                }

                return true;
            }).sort((a, b) => {
                // 
                const { prop, order } = this.sortBy;
                if (order === 'ascending') {
                    return a[prop] > b[prop] ? 1 : -1;
                }
                return a[prop] < b[prop] ? 1 : -1;
            });
        },

        pagedData() {
            const start = (this.currentPage - 1) * this.pageSize;
            return this.filteredData.slice(start, start + this.pageSize);
        },

        scoreCount() {
            return this.filteredData.filter(record => record.score !== null).length;
        }
    },

    methods: {
        handleFilter() {
            this.currentPage = 1;
        },

        resetFilters() {
            this.filters = {
                keyword: '',
                event: '',
                phase: '',
                hasScore: ''
            };
            this.handleFilter();
        },

        handleSortChange({ prop, order }) {
            // 
            if (!order) {
                this.sortBy = { prop: 'timestamp', order: 'descending' };
                return;
            }
            this.sortBy = { prop, order };
        },

        handleSizeChange(val) {
            this.pageSize = val;
            this.currentPage = 1;
        },

        handleCurrentChange(val) {
            this.currentPage = val;
        },

        formatTime(timestamp) {
            return new Date(timestamp).toLocaleString('zh-CN', {
                year: 'numeric',
                month: '2-digit',
                day: '2-digit',
                hour: '2-digit',
                minute: '2-digit'
            });
        },

        formatScore(score) {
            return `${score}${score > 60 ? '' : ''}`;
        },

        getEventTagType(phase) {
            const types = {
                preliminary: 'info',
                semifinal: 'warning',
                final: 'success'
            };
            return types[phase] || 'info';
        },

        getPhaseText(phase) {
            const texts = {
                preliminary: '',
                semifinal: '',
                final: ''
            };
            return texts[phase] || phase;
        },

        getRankTagType(rank) {
            if (rank <= 3) return 'success';
            if (rank <= 8) return 'warning';
            return 'info';
        },

        exportToExcel() {
            // 
            this.$message.success('');
        },
    }
}
</script>

