<template>
    <div class="sport-meeting-settings">
        <!--  -->
        <div class="settings-container">
            <div class="content-wrapper">
                <!--  -->
                <el-row :gutter="20" class="top-row">
                    <el-col :xs="24" :sm="24" :md="8">
                        <!--  -->
                        <div class="section">
                            <h3 class="section-title"></h3>
                            <el-form :model="basicForm" ref="basicForm" :rules="basicRules" label-width="120px"
                                class="basic-form">
                                <el-form-item label="" prop="name">
                                    <el-input v-model="basicForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="cover" class="cover-form-item">
                                    <div style="margin-top: 12px;margin-left:32px">
                                        <image-uploader :imageUrl.sync="coverPreview" :file.sync="basicForm.coverFile">
                                        </image-uploader>
                                    </div>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="handleBasicSave"></el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-col>
                    <el-col :xs="24" :sm="24" :md="16">
                        <!--  -->
                        <div class="section">
                            <h3 class="section-title"></h3>
                            <el-form :model="timeForm" ref="timeForm" :rules="timeRules" label-width="120px">
                                <el-form-item label="" required class="time-range">
                                    <div class="time-inputs">
                                        <el-form-item prop="startTime" class="time-input-item">
                                            <el-date-picker type="datetime" placeholder=""
                                                v-model="timeForm.startTime" style="width: 100%;"
                                                value-format="timestamp" :default-value="new Date()">
                                            </el-date-picker>
                                        </el-form-item>
                                        <el-form-item prop="endTime" class="time-input-item">
                                            <el-date-picker type="datetime" placeholder=""
                                                v-model="timeForm.endTime" style="width: 100%;" value-format="timestamp"
                                                :default-value="new Date()">
                                            </el-date-picker>
                                        </el-form-item>
                                    </div>
                                </el-form-item>

                                <el-form-item label="" required class="time-range">
                                    <div class="time-inputs">
                                        <el-form-item prop="amStartTime" class="time-input-item">
                                            <el-time-picker placeholder="" v-model="timeForm.amStartTime"
                                                style="width: 100%;" value-format="HH:mm:ss"
                                                :default-value="new Date()">
                                            </el-time-picker>
                                        </el-form-item>
                                        <el-form-item prop="amEndTime" class="time-input-item">
                                            <el-time-picker placeholder="" v-model="timeForm.amEndTime"
                                                style="width: 100%;" value-format="HH:mm:ss"
                                                :default-value="new Date()">
                                            </el-time-picker>
                                        </el-form-item>
                                    </div>
                                </el-form-item>

                                <el-form-item label="" required class="time-range">
                                    <div class="time-inputs">
                                        <el-form-item prop="pmStartTime" class="time-input-item">
                                            <el-time-picker placeholder="" v-model="timeForm.pmStartTime"
                                                style="width: 100%;" value-format="HH:mm:ss"
                                                :default-value="new Date()">
                                            </el-time-picker>
                                        </el-form-item>
                                        <el-form-item prop="pmEndTime" class="time-input-item">
                                            <el-time-picker placeholder="" v-model="timeForm.pmEndTime"
                                                style="width: 100%;" value-format="HH:mm:ss"
                                                :default-value="new Date()">
                                            </el-time-picker>
                                        </el-form-item>
                                    </div>
                                </el-form-item>

                                <el-form-item label="" required prop="registrationDeadline">
                                    <el-date-picker v-model="timeForm.registrationDeadline" type="datetime"
                                        placeholder="" style="width: 100%;" value-format="timestamp"
                                        :default-value="new Date()">
                                    </el-date-picker>
                                    <div class="time-tip"></div>
                                </el-form-item>

                                <el-form-item>
                                    <el-button type="primary" @click="handleTimeSave"></el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-col>
                </el-row>

                <!--  -->
                <div class="section" style="margin-bottom: 20px;">
                    <h3 class="section-title"></h3>
                    <div class="lifecycle-track">
                        <div class="status-point" v-for="(stage, index) in stages" :key="index" :class="{
                            'active': meetingInfo.status >= index + 1,
                            'current': meetingInfo.status === index + 1,
                            'clickable': index + 1 !== 3  // 
                        }" @click="index + 1 !== 3 && showStageInfo(index + 1)">
                            <div class="point"></div>
                            <div class="label">{{ stage.label }}</div>
                        </div>
                        <div class="progress-line" :style="{ width: progressWidth }"></div>
                    </div>
                </div>

                <!--  -->
                <div class="section">
                    <h3 class="section-title"></h3>
                    <el-tabs v-model="activeTab">
                        <el-tab-pane label="" name="staff">
                            <div class="action-bar">
                                <el-button type="primary" @click="showAddStaffDialog"></el-button>
                            </div>
                            <el-table :data="staffList" border @row-click="handleStaffRowClick">
                                <el-table-column prop="username" label="" width="150">
                                    <template slot-scope="scope">
                                        <div class="clickable-cell">{{ scope.row.username }}</div>
                                    </template>
                                </el-table-column>
                                <el-table-column label="">
                                    <template slot-scope="scope">
                                        <div class="clickable-cell">
                                            <el-tag v-for="type in scope.row.userType" :key="type"
                                                :type="getTypeTag(type)" class="user-type-tag">
                                                {{ getTypeLabel(type) }}
                                            </el-tag>
                                        </div>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" width="100" align="right">
                                    <template slot-scope="scope">
                                        <div class="operation-buttons" @click.stop>
                                            <template v-if="scope.row.isEditing">
                                                <el-button size="mini" type="success"
                                                    @click="saveStaffEdit(scope.row)"></el-button>
                                                <el-button size="mini" type="info"
                                                    @click="cancelStaffEdit(scope.row)"></el-button>
                                            </template>
                                            <template v-else>
                                                <el-button size="mini" type="text" class="delete-btn"
                                                    @click="handleDeleteStaff(scope.row)">
                                                    <i class="el-icon-close"></i>
                                                </el-button>
                                                <el-button size="mini" type="text" class="edit-btn"
                                                    @click="handleEditStaff(scope.row)">
                                                    <i class="el-icon-edit"></i>
                                                </el-button>
                                            </template>
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>

                        <el-tab-pane label="" name="invites">
                            <div class="action-bar">
                                <el-button type="primary" @click="showAddInviteDialog"></el-button>
                            </div>
                            <el-table :data="inviteList" border>
                                <el-table-column prop="code" label="" width="100">
                                    <template slot-scope="scope">
                                        <div class="clickable-cell" @click="handleEditInvite(scope.row)">
                                            {{ scope.row.code }}
                                        </div>
                                    </template>
                                </el-table-column>
                                <el-table-column label="">
                                    <template slot-scope="scope">
                                        <div class="clickable-cell" @click="handleEditInvite(scope.row)">
                                            <el-tag v-for="type in scope.row.userType" :key="type"
                                                :type="getTypeTag(type)" class="user-type-tag">
                                                {{ getTypeLabel(type) }}
                                            </el-tag>
                                        </div>
                                    </template>
                                </el-table-column>
                                <el-table-column prop="expireTime" label="">
                                    <template slot-scope="scope">
                                        <div class="clickable-cell" @click="handleEditInvite(scope.row)">
                                            {{ formatTime(scope.row.expireTime) }}
                                        </div>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" width="80" align="right">
                                    <template slot-scope="scope">
                                        <el-button size="mini" type="text" class="delete-btn" icon="el-icon-close"
                                            circle @click.stop="handleDeleteInvite(scope.row)">
                                        </el-button>
                                        <el-button size="mini" type="text" class="edit-btn" icon="el-icon-edit" circle
                                            @click.stop="handleEditInvite(scope.row)">
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>
                    </el-tabs>
                </div>

                <!--  -->
                <div class="danger-zone">
                    <h3 class="section-title"></h3>
                    <el-button type="danger" plain @click="showDeleteDialog = true">
                        
                    </el-button>
                </div>

                <responsive-dialog :visible.sync="showDeleteDialog" title="" width="400px"
                    :hide-default-footer="true">
                    <div class="delete-warning">
                        
                        <p class="warning-detail">
                            
                        </p>
                    </div>
                    <template #footer>
                        <div class="custom-footer">
                            <el-button type="danger" @click="handleDelete"></el-button>
                            <el-button type="primary" @click="showDeleteDialog = false"></el-button>
                        </div>
                    </template>
                </responsive-dialog>

                <!-- Staff -->
                <responsive-dialog :visible.sync="staffDialog.visible" :title="staffDialog.title" width="500px"
                    @confirm="handleStaffConfirm" @cancel="staffDialog.visible = false">
                    <el-form ref="staffForm" :model="staffForm" :rules="staffRules" label-width="100px">
                        <template v-if="!staffDialog.isEdit">
                            <el-radio-group v-model="staffForm.addType" class="add-type-radio"
                                style="margin-bottom: 20px;">
                                <el-radio :label="1"></el-radio>
                                <el-radio :label="2"></el-radio>
                            </el-radio-group>

                            <!--  -->
                            <template v-if="staffForm.addType === 1">
                                <el-form-item label="" prop="username">
                                    <el-input v-model="staffForm.username"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="password">
                                    <el-input type="password" v-model="staffForm.password"></el-input>
                                </el-form-item>
                            </template>

                            <!--  -->
                            <template v-if="staffForm.addType === 2">
                                <el-form-item label="" prop="inviteCode">
                                    <el-input v-model="staffForm.inviteCode">
                                        <el-button slot="append" @click="generateInviteCode"></el-button>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="" prop="expireTime">
                                    <el-date-picker v-model="staffForm.expireTime" type="datetime" placeholder=""
                                        value-format="timestamp" :picker-options="{
                                            disabledDate(time) {
                                                return time.getTime() < Date.now() - 8.64e7;
                                            }
                                        }">
                                    </el-date-picker>
                                </el-form-item>
                            </template>
                        </template>

                        <el-form-item label="" prop="userType">
                            <el-checkbox-group v-model="staffForm.userType">
                                <el-checkbox :label="1"></el-checkbox>
                                <el-checkbox :label="2"></el-checkbox>
                                <el-checkbox :label="3"></el-checkbox>
                                <el-checkbox :label="4"></el-checkbox>
                                <el-checkbox :label="5"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                        <!--  -->
                        <el-form-item label="" prop="expireTime" v-if="staffDialog.isEdit && staffForm.code">
                            <el-date-picker v-model="staffForm.expireTime" type="datetime" placeholder=""
                                value-format="timestamp" :picker-options="{
                                    disabledDate(time) {
                                        return time.getTime() < Date.now() - 8.64e7;
                                    }
                                }">
                            </el-date-picker>
                        </el-form-item>

                        <!--  -->
                        <div v-if="staffForm.error" class="error-tip">{{ staffForm.error }}</div>
                    </el-form>
                </responsive-dialog>

                <!--  -->
                <responsive-dialog v-model="dialogVisible" :title="currentStage?.label || ''" width="500px"
                    :hideDefaultFooter="true" :visible.sync="dialogVisible">
                    <div class="stage-info">
                        <p>{{ currentStage?.description || '' }}</p>
                    </div>
                </responsive-dialog>

            </div>
        </div>
    </div>
</template>

<script>
import request from '@/utils/request'
import ResponsiveDialog from '@/components/ResponsiveDialog.vue'
import dayjs from 'dayjs'
import ImageUploader from '@/components/ImageUploader.vue'

export default {
    name: 'SportMeetingSettings',
    components: {
        ResponsiveDialog,
        ImageUploader
    },
    data() {
        return {
            meetingInfo: {
                status: 1, // 
                startTime: '',
                endTime: '',
                registrationDeadline: ''
            },
            dialogVisible: false,
            currentStage: null,
            stages: [
                {
                    label: '',
                    status: 1,
                    description: ''
                },
                {
                    label: '',
                    status: 2,
                    description: ' \n '
                },
                {
                    label: '',
                    status: 3,
                    description: ''
                }
            ],
            activeTab: 'staff',
            isSchoolMode: true, // API
            staffList: [],
            inviteList: [],
            staffDialog: {
                visible: false,
                title: '',
                isEdit: false
            },
            uploadUrl: process.env.VUE_APP_BASE_API + '/uploadCover',
            staffForm: {
                addType: 1,
                username: '',
                password: '',
                inviteCode: '',
                expireTime: '',
                userType: [],
                uid: '',
                error: '' // 
            },
            staffRules: {
                username: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                inviteCode: [
                    { required: true, message: '', trigger: 'blur' },
                    { pattern: /^[a-zA-Z0-9_]+$/, message: '', trigger: 'blur' }
                ],
                expireTime: [
                    { required: true, message: '', trigger: 'change' }
                ],
                userType: [
                    { required: true, message: '', trigger: 'change' }
                ]
            },
            basicForm: {
                name: '',
                cover: '',
                coverFile: null // 
            },
            coverPreview: '', // URL
            timeForm: {
                startTime: '',
                endTime: '',
                amStartTime: '',
                amEndTime: '',
                pmStartTime: '',
                pmEndTime: '',
                registrationDeadline: ''
            },
            basicRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            timeRules: {
                startTime: [{ required: true, message: '', trigger: 'change' }],
                endTime: [{ required: true, message: '', trigger: 'change' }],
                amStartTime: [{ required: true, message: '', trigger: 'change' }],
                amEndTime: [{ required: true, message: '', trigger: 'change' }],
                pmStartTime: [{ required: true, message: '', trigger: 'change' }],
                pmEndTime: [{ required: true, message: '', trigger: 'change' }],
                registrationDeadline: [{ required: true, message: '', trigger: 'change' }]
            },
            showDeleteDialog: false
        }
    },
    computed: {
        progressWidth() {
            const status = this.meetingInfo?.status || 0
            if (status <= 1) return '0%'
            if (status === 2) return '50%'
            return '100%'
        }
    },
    methods: {
        getTypeTag(type) {
            const tags = {
                1: '',
                2: 'success',
                3: 'warning'
            }
            return tags[type] || 'info'
        },
        getTypeLabel(type) {
            const types = {
                1: '',
                2: '',
                3: '',
                4: '',
                5: ''
            }
            return types[type] || ''
        },
        formatTime(time) {
            if (!time) return '';
            const date = typeof time === 'string' ? new Date(time) : new Date(Number(time));
            return dayjs(date).format('YYYY-MM-DD HH:mm:ss');
        },
        generateInviteCode() {
            const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789_'
            let code = ''
            for (let i = 0; i < 8; i++) {
                code += chars.charAt(Math.floor(Math.random() * chars.length))
            }
            this.staffForm.inviteCode = code
        },
        showAddStaffDialog() {
            this.staffDialog.title = ''
            this.staffDialog.isEdit = false
            this.staffDialog.visible = true
            this.staffForm = {
                addType: 1, // 
                username: '',
                password: '',
                inviteCode: '',
                expireTime: '',
                userType: [],
                error: ''
            }
        },
        showAddInviteDialog() {
            // alert("")
            this.staffDialog.title = ''
            this.staffDialog.isEdit = false
            this.staffDialog.visible = true
            this.staffForm = {
                addType: 2, // 
                username: '',
                password: '',
                inviteCode: '',
                expireTime: '',
                userType: [],
                error: ''
            }
            this.generateInviteCode() // 
        },
        async handleStaffConfirm() {
            try {
                this.staffForm.error = '' // 
                await this.$refs.staffForm.validate()
                if (this.staffDialog.isEdit) {
                    if (this.staffForm.code) {
                        //  - 
                        const res = await request({
                            url: '/updateInviteCodePower',
                            method: 'put',
                            data: {
                                code: this.staffForm.code,
                                userType: this.staffForm.userType
                            }
                        })
                        if (res.code === 1) {
                            this.$message.success('')
                            this.staffDialog.visible = false
                            this.fetchInviteList()
                        }
                    } else {
                        //  - 
                        const res = await request({
                            url: '/updateUserPower',
                            method: 'put',
                            data: {
                                uid: this.staffForm.uid,
                                userType: this.staffForm.userType
                            }
                        })
                        if (res.code === 1) {
                            this.$message.success('')
                            this.staffDialog.visible = false
                            this.fetchStaffList()
                        }
                    }
                } else if (this.staffForm.addType === 1) {
                    //  - 
                    const registerInfo = new FormData()
                    registerInfo.append('name', this.staffForm.username)
                    registerInfo.append('password', this.staffForm.password)
                    registerInfo.append('powerDegree', JSON.stringify(this.staffForm.userType))

                    const res = await request({
                        url: '/register',
                        method: 'post',
                        data: registerInfo
                    })

                    if (res.code === 1) {
                        this.$message.success('')
                        this.staffDialog.visible = false
                        this.fetchStaffList()
                    }
                } else {
                    // 
                    const data = {
                        code: this.staffForm.inviteCode,
                        userType: this.staffForm.userType,
                        expireTime: new Date(this.staffForm.expireTime).toISOString()
                    }

                    const res = await request({
                        url: '/createInviteCode',
                        method: 'post',
                        data
                    })

                    if (res.code === 1) {
                        this.$message.success('')
                        this.staffDialog.visible = false
                        this.fetchInviteList()
                    } else if (res.code === 0 && res.message === 'DUPLICATED_CODE') {
                        // 
                        this.staffForm.error = ''
                    }
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        async handleBasicSave() {
            try {
                await this.$refs.basicForm.validate()
                const formData = new FormData()
                formData.append('name', this.basicForm.name)
                if (this.basicForm.coverFile) {
                    formData.append('cover', this.basicForm.coverFile)
                }

                const res = await request({
                    url: '/updateMeetingBasic',
                    method: 'put',
                    data: formData,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                })
                if (res.code === 1) {
                    this.$message.success('')
                    if (res.data && res.data.coverUrl) {
                        this.basicForm.cover = res.data.coverUrl
                    }
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        async handleTimeSave() {
            try {
                await this.$refs.timeForm.validate()

                // : /ISO,HH:mm:ss
                const formData = {
                    startTime: new Date(this.timeForm.startTime).toISOString(),  // ISO
                    endTime: new Date(this.timeForm.endTime).toISOString(),      // ISO
                    amStartTime: this.timeForm.amStartTime,     // HH:mm:ss
                    amEndTime: this.timeForm.amEndTime,         // HH:mm:ss
                    pmStartTime: this.timeForm.pmStartTime,     // HH:mm:ss
                    pmEndTime: this.timeForm.pmEndTime,          // HH:mm:ss
                    registrationDeadline: new Date(this.timeForm.registrationDeadline).toISOString() // ISO
                }

                const res = await request({
                    url: '/updateMeetingTime',
                    method: 'put',
                    data: formData
                })
                if (res.code === 1) {
                    this.$message.success('')
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        async fetchMeetingInfo() {
            try {
                const res = await request({
                    url: '/getMeetingInfo',
                    method: 'get'
                })
                if (res.code === 1) {
                    const data = res.data
                    // meetingInfo
                    this.meetingInfo = {
                        status: data.status,
                        startTime: data.startTime,
                        endTime: data.endTime,
                        registrationDeadline: data.registrationDeadline
                    }

                    // 
                    this.basicForm.name = data.name
                    this.basicForm.cover = data.img

                    // 
                    const startDate = new Date(data.startTime)
                    const endDate = new Date(data.endTime)
                    const registrationDeadlineDate = new Date(data.registrationDeadline)

                    this.timeForm = {
                        startTime: startDate.getTime(),
                        endTime: endDate.getTime(),
                        amStartTime: data.amStartTime,
                        amEndTime: data.amEndTime,
                        pmStartTime: data.pmStartTime,
                        pmEndTime: data.pmEndTime,
                        registrationDeadline: registrationDeadlineDate.getTime()
                    }
                }
            } catch (error) {
                console.error(':', error)
                this.$message.error('')
            }
        },
        async handleEditStaff(staff) {
            this.staffDialog.title = ''
            this.staffDialog.isEdit = true
            this.staffDialog.visible = true
            // 
            this.staffForm = {
                username: staff.username,
                userType: staff.userType,
                uid: staff.uid
            }
        },
        async handleEditInvite(invite) {
            this.staffDialog.title = ''
            this.staffDialog.isEdit = true
            this.staffDialog.visible = true
            this.staffForm = { ...invite }
        },
        async handleDeleteStaff(staff) {
            try {
                await this.$confirm('')
                const res = await request({
                    url: `/deleteStaff/${staff.uid}`,  // uidid
                    method: 'delete'
                })

                if (res.code === 1) {
                    this.$message.success('')
                    this.fetchStaffList()
                }
            } catch (error) {
                if (error !== 'cancel') {
                    console.error(':', error)
                }
            }
        },
        async handleDeleteInvite(invite) {
            try {
                await this.$confirm('')
                const res = await request({
                    url: `/deleteInviteCode/${invite.code}`,
                    method: 'delete'
                })

                if (res.code === 1) {
                    this.$message.success('')
                    this.fetchInviteList()
                }
            } catch (error) {
                if (error !== 'cancel') {
                    console.error(':', error)
                }
            }
        },
        async fetchStaffList() {
            try {
                const res = await request({
                    url: '/getStaffList',
                    method: 'get'
                })

                if (res.code === 1) {
                    // 
                    this.staffList = res.data.map(item => ({
                        uid: item.uid,
                        username: item.name,
                        //  powerDegree 
                        userType: JSON.parse(item.powerDegree || '[]')
                    }))
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        async fetchInviteList() {
            try {
                const res = await request({
                    url: '/getInviteCodes',
                    method: 'get'
                })

                if (res.code === 1) {
                    this.inviteList = res.data
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        handleCoverSuccess(res, /* file */) {
            if (res.code === 1) {
                this.basicForm.cover = res.data.url
            } else {
                this.$message.error('')
            }
        },
        beforeCoverUpload(file) {
            // 
            if (!file.type.startsWith('image/')) {
                this.$message.error('!')
                return false
            }

            // 
            const reader = new FileReader()
            reader.onload = (e) => {
                this.coverPreview = e.target.result
                // Vue
                this.$forceUpdate()
            }
            reader.readAsDataURL(file)

            // 
            this.basicForm.coverFile = file
            return false // 
        },
        handleRowClick(row) {
            if (this.activeTab === 'staff') {
                // 
                row.isEditing = !row.isEditing;
                // ,
                if (!row.isEditing) {
                    this.fetchStaffList(); // 
                }
            }
        },
        saveStaffEdit(staff) {
            this.handleEditStaff(staff);
        },
        cancelStaffEdit(staff) {
            staff.isEditing = false;
            this.fetchStaffList(); // 
        },
        async handleDelete() {
            try {
                const res = await request({
                    url: '/deleteMeeting',
                    method: 'delete'
                })

                if (res.code === 1) {
                    this.$message.success('')
                    this.$router.push('/sport-meetings')
                }
            } catch (error) {
                console.error(':', error)
                this.$message.error(error.message || '')
            } finally {
                this.showDeleteDialog = false
            }
        },
        showStageInfo(status) {
            const stage = this.stages.find(s => s.status === status)
            if (stage) {
                this.currentStage = stage
                this.dialogVisible = true // 
            }
        },
        formatTimeInfo(template) {
            if (!template) return ''
            return template
                .replace('{{ registrationDeadline }}', this.formatDateTime(this.meetingInfo?.registrationDeadline))
                .replace('{{ startTime }}', this.formatDateTime(this.meetingInfo?.startTime))
                .replace('{{ endTime }}', this.formatDateTime(this.meetingInfo?.endTime))
        },
        formatDateTime(dateStr) {
            if (!dateStr) return ''
            const date = new Date(dateStr)
            return date.toLocaleString('zh-CN', {
                year: 'numeric',
                month: '2-digit',
                day: '2-digit',
                hour: '2-digit',
                minute: '2-digit'
            })
        },
        handleStaffRowClick(row) {
            this.handleEditStaff(row);
        }
    },
    mounted() {
        this.fetchMeetingInfo()
        this.fetchStaffList()
        this.fetchInviteList()
        // 
        if (this.basicForm.cover) {
            this.coverPreview = this.basicForm.cover
        }
    },
    watch: {
        //  cover 
        'basicForm.cover': {
            handler(newVal) {
                if (newVal && !this.coverPreview) {
                    this.coverPreview = newVal
                }
            },
            immediate: true
        }
    }
}
</script>

