    // padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    min-height: auto; // 

    .action-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;
        padding: 0 16px;
        flex-wrap: wrap;
        gap: 16px;

        .left-section {
            display: flex;
            align-items: center;
        }

        .filter-container {
            display: flex;
            align-items: center;
            gap: 8px;
            flex-wrap: wrap;
            width: 100%;

            .rule-config-btn {
                margin-left: auto;
                border-radius: 4px;

                @media screen and (max-width: 768px) {
                    width: 100%;
                    margin-top: 8px;
                }

                i {
                    margin-right: 4px;
                }
            }

            @media screen and (max-width: 768px) {
                width: 100%;
                justify-content: space-between;
            }
        }
    }

    .competition-list {
        display: grid;
        gap: 16px;
        grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
        transition: all 0.3s ease;
        padding: 0 16px;

        &.has-active {
            grid-template-columns: 1fr;
        }
    }

    // Card animations
    .card-list-enter-active,
    .card-list-leave-active {
        transition: all 0.3s ease;
    }

    .card-list-enter,
    .card-list-leave-to {
        opacity: 0;
        transform: translateY(30px);
    }

    .card-list-move {
        transition: transform 0.5s ease;
    }

    @media (max-width: 768px) {
        padding: 12px;

        .action-bar {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    @media screen and (max-width: 768px) {
        padding: 0;
        margin-bottom: 60px; // 

        .competition-list {
            padding: 10px;
            gap: 10px;
        }
    }
}
</style>
